// Ensure the component host element fills the drag clone container
blog-header-drag-clone {
  display: block;
  width: 100%;
  height: 100%;
}

.header-preview {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 8px;
  background: linear-gradient(135deg, #f0f4f8 0%, #e8f0f2 100%);
  padding: 16px;

  .header-title {
    color: #2c3e50;
    font-size: clamp(14px, 2.5vw, 20px);
    font-weight: 700;
    text-align: center;
  }

  .header-subtitle {
    color: #6c757d;
    font-size: clamp(11px, 1.8vw, 14px);
    font-style: italic;
    text-align: center;
  }
}
