// Ensure the component host element fills the drag clone container
blog-button-drag-clone {
  display: block;
  width: 100%;
  height: 100%;
}

.button-preview {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  background: linear-gradient(135deg, #4a90e2 0%, #357abd 100%);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);

  span {
    color: #ffffff;
    font-size: clamp(12px, 2vw, 16px);
    font-weight: 600;
  }
}
